.contact {
  &__pictures {
    background-color: $color-primary;
    grid-column: col-start 5 / full-end;
    background-image: linear-gradient(rgba($color-primary-dark, 0.3), rgba($color-primary-dark, 0.3)), url(../../img/temp_pic.jpg);
    background-size: cover;
    background-position: center;

    @media only screen and (max-width: $bp-medium) {
      grid-column: 1 / -1;
      padding: 6rem;
    }
  }

  &__content {
    background-color: $color-grey-light-1;
    grid-column: full-start / col-end 4;

    padding: 6rem 8vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    @media only screen and (max-width: $bp-medium) {
      grid-column: 1 / -1;
      grid-row: 5 / 6;
    }

    /* Grid Method
      display: grid;
      align-content: center;
      justify-items: start;
      */
  }

  &__text {
    font-size: 1.5rem;
    font-style: italic;
    margin-bottom: 4rem;
  }
}

.form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;

  h2 {
    grid-column: 1 / -1;
    color: $color-secondary;
  }

  &__group {
  }

  &__label {
    display: block;
  }

  &__input {
    &--message {
      width: 100%;
    }
  }
}

.full {
  grid-column: 1 / -1;
}
